body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  touch-action: manipulation;
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 58px
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 58px
}

#modal {
  position: relative;
  z-index: 999
}

.padding {
  padding: 10px 10px 10px 10px;
}

.settings-modal {
  background-color: rgba(0,0,0,0.5);
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.settings-modal-content {
  background-color: white;
  text-align: center;
}
.TallyRowContainer {
  display: grid;
  /* grid-template-columns: 1fr 1.5fr 1fr; */
  grid-template-rows: 150px 150px 150px 150px;
  grid-gap: 3px;
}

.TallyRowElement {
  display: flex;
  justify-content: center;
  align-items: center;
}

.TallyRow-name {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1em;  
}

.TallyRow-value {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 4em;  
}

